class DrawerContent extends Component{
    render(){
        const {remove}=this.props;
        return (
            <div>
                <h1>Drawer</h1>
                <Button size="small" onClick={remove}>Drawer</Button>
            </div>
        );
    }
}

ReactDOM.render(<div>
    <Button type="primary" size="small" onClick={function(){new Drawer(<div><h1>Drawer</h1></div>);}}></Button>
    <Button type="primary" size="small" onClick={function(){new Drawer(<div><h1>Drawer</h1></div>,{right:true});}}></Button>
    <Button type="primary" size="small" onClick={function(){new Drawer(<DrawerContent/>);}}></Button>
</div>,document.querySelector('.s-dialog-drawer'));


class AnimateList extends Component{
    itemList=[<ListItem key="0">1</ListItem>,
    <ListItem key="1">2</ListItem>,
    <ListItem key="2">3</ListItem>,
    <ListItem key="3">4</ListItem>]
    state={
        show:false
    }
    renderList(){
        return <List animate>
            {this.state.show?this.itemList:null}
        </List>
    }
    swatchList=()=>{
        this.setState({
            show:!this.state.show
        });
    }
    render(){
        return <div>
            <Button type="primary" size="small" onClick={this.swatchList}>{this.state.show?'':''}</Button>
            {this.renderList()}
        </div>
    }
}
